<template>
  <div>
    <nav-head title="设置" FixedOrTransparency="isFixed NoTransparency"/>
    <div class="set-list">
      <router-link to="resetPassword" tag="div" class="item">
        <span class="icon-pwd icon"></span>
        <span>重置密码</span>
        <span class="arrow-right"></span>
      </router-link>
      <router-link to="aboutUs" tag="div" class="item">
        <span class="icon-about icon"></span>
        <span>关于我们</span>
        <span class="arrow-right"></span>
      </router-link>
    </div>
    <mt-button size="large" @click.native="onShowDialog" class="btn-logout">退出登录</mt-button>
    <div class="cancel-bind" :class="{dialogShow: dialogShow}">
      <div class="mask" @click="onCloseDialog"></div>
      <div class="content">
        <div class="message">确认注销登录状态吗？</div>
        <div class="btn-group">
          <mt-button @click.native="onCloseDialog" class="cancelButton">取消</mt-button>
          <mt-button @click.native="logout" class="confirmButton">确认</mt-button>
        </div>
      </div>
    </div>
  </div>
</template>
<script>//
import NavHead from '@/components/NavHead.vue'
import { Button } from 'mint-ui'
import { mapActions } from 'vuex'
import { LOGOUT } from '../../../vuex/Mine'

export default {
  name: 'set',
  data: () => {
    return {
      dialogShow: false
    }
  },
  methods: {
    onShowDialog () {
      this.dialogShow = true
    },
    onCloseDialog () {
      this.dialogShow = false
    },
    ...mapActions({
      logout: LOGOUT
    })
  },
  components: {
    NavHead,
    [Button.name]: Button
  }
}
</script>
<style scoped lang="scss">
  div.set-list {
    background-color: $background-light-color;
    padding: 0 15px;
    margin-top: 15px;
    .item {
      height: 54px;
      line-height: 54px;
      text-align: left;
      font-size: 16px;
      color: $font-color;
      font-weight: bold;
      position: relative;
      border-bottom: 1px solid $border-color;
      > span.icon {
        position: absolute;
        margin-top: 15px;
        left: 0;
      }
      > span.arrow-right {
        position: absolute;
        right: 0;
        top: 15px;
      }
      span:nth-child(2) {
        margin-left: 30px;
      }
    }
    .item:last-child {
      border-bottom: none;
    }
  }
  .btn-logout {
    background-color: $background-light-color;
    color: $back-color;
    font-size: 16px;
    font-weight: bold;
    border: none;
    box-shadow: none;
    padding: 0;
    height: 55px;
    line-height: 55px;
    border-radius: 0;
    margin-top: 15px;
  }
  div.cancel-bind {
    position: absolute;
    z-index: 2;
    width: 100%;
    height: 100%;
    max-width: 640px;
    top: 0;
    display: none;
    > div.mask {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      background-color: rgba(0,0,0,0.5);
      z-index: 2;
    }
    > div.content {
      position: relative;
      z-index: 3;
      width: 70%;
      margin-left: 15%;
      background-color: white;
      border-radius: 6px;
      padding: 15px 0;
      top: calc((100vh - 162px)/2);
      text-align: center;
      > div.message {
        font-size: 16px;
        color: #333;
        font-weight: bold;
        line-height: 26px;
        margin: 15px 0 25px 0;
      }
      .cancelButton{
        background: #DDD ;
        width: 40% ;
        height: 40px;
        color: #999;
        margin: 0 15px 0 0 ;
        font-size: 16px;
      }
      .confirmButton{
        background-color: $back-color;
        @if $IF365 { color: #131313; }
        @else { color: #FFF; }
        width: 40% ;
        height: 40px;
        font-size: 16px;
      }
    }
  }
  .cancel-bind.dialogShow {
    display: block;
  }
</style>
